<template>
  <div style="padding: 0 20px">
    <div class="logintitle">
      <img src="../../image/logo.jpg" style="width: 100px; height: 100px" alt />
      <!-- <div>商机管理</div> -->
    </div>
    <van-form @submit="onSubmit">
      <van-field
        v-model="username"
        name="手机号码"
        label="手机号码:"
        placeholder="请填写手机号码"
        :rules="[{ required: true, message: '' }]"
      />
      <van-field
        v-model="password"
        type="password"
        name="密码"
        label="密码:"
        placeholder="请填写密码"
        :rules="[{ required: true, message: '' }]"
      />
      <van-field
        v-model="code"
        center
        clearable
        label="验证码"
        placeholder="请填写验证码"
        :rules="[{ required: true, message: '' }]"
      >
        <template #button>
          <van-button style="width: 65px;" size="small" type="info" @click="sendMsg()">{{time}}</van-button>
        </template>
      </van-field>
      <div style="margin-top: 36px; padding: 0 20px">
        <van-button round block type="info" native-type="submit"
          >登录</van-button
        >
      </div>
    </van-form>
  </div>
</template>

<script>
import { Form, Field, Icon, Button, Toast } from "vant";

export default {
  components: {
    [Form.name]: Form,
    [Icon.name]: Icon,
    [Field.name]: Field,
    [Button.name]: Button,
  },
  data() {
    return {
      username: "",
      password: "",
      code:"",
      time:"验证码",
      codeInterval:null
    };
  },
  created() {
    // localStorage.setItem("token","");
  },
  methods: {
    // eslint-disable-next-line no-unused-vars
    onSubmit(values) {
      let that = this;
      // eslint-disable-next-line no-console
      // console.log("submit");
      this.$http
        .post("/admin/login/index", {
          account: this.username,
          password: this.password,
          code:this.code
        })
        .then(function (response) {
          // eslint-disable-next-line no-console
          console.log(response.data);

          if (response.data.code === 2000) {
            window.localStorage.setItem("token", response.data.data.token);
            window.localStorage.setItem("username",response.data.data.username);
            window.localStorage.setItem("is_admin",response.data.data.is_admin);
            window.localStorage.setItem("is_depart_admin",response.data.data.is_depart_admin);
            that.$router.push("/");
          }else{
             Toast("登录失败");
          }
        })
        // eslint-disable-next-line no-unused-vars
        .catch(function (error) {
          // eslint-disable-next-line no-mixed-spaces-and-tabs
          Toast("登录失败");
        });
    },
    sendMsg(){
       let that = this;
        if(this.time != "验证码"){
             return;
        }
        
        if(this.username == ""){
              Toast("请输入用户名");
              return;
        }

          if(this.password == ""){
              Toast("请输入密码");
              return;
        }
         
         this.reduceTime();
         this.$http
        .post("/sms/get_sms_code", {
          mobile: this.username,
          type: "login",
        })
        .then(function (response) {
          // eslint-disable-next-line no-console
          console.log(response.data);

          if (response.data && response.data.code === 2000) {
               Toast("发送验证码成功");
         
          }else{
              if(that.codeInterval){
                clearInterval(that.codeInterval)
                that.time = '验证码';
              }
              Toast(response.data.data);
          }
        })
        // eslint-disable-next-line no-unused-vars
        .catch(function (error) {
          if(that.codeInterval){
            clearInterval(that.codeInterval)
            that.time = '验证码';
          }
          // eslint-disable-next-line no-mixed-spaces-and-tabs
          Toast("网络错误!");
        });
    },
    reduceTime(){
        this.time = 59;
        var that = this;
        this.codeInterval = setInterval(function(){
            that.time = that.time - 1;
            if(that.time === 0) {
                that.time = '验证码';
                clearInterval(that.codeInterval);
                that.codeInterval = null;
            }
        },1000)
        }
  },
};
</script>

<style>
body {
  background: white;
}
.logintitle {
  padding-top: 120px;
  padding-bottom: 10px;
  color: #409eff;
  text-align: center;
}
</style>
